@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');

body {
  background-image: url("/sw/images/background-smaller.jpg");
  background-size: 100% auto;
  background-repeat: repeat-y;
  background-color: #FAFAFA;
}

.App {
  text-align: center;
  max-width: 992px;
  min-width: 360px;
  padding-left: 12%;
  padding-right: 12%;
}

.App-link {
  color: #61dafb;
}

.App-logo {
  width: 80%;
  max-width: 496px;
  margin-top: 30px;
}

.img-shadow {
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
}

.top-spacing {
  margin-top: 3vh;
}

.top-spacing-6 {
  margin-top: 6vh;
}

.bottom-spacing {
  margin-bottom: 3vh;
}

.title {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  color: #5F2715;
}

.subtitle {
  font-weight: 200;
}

.bottom-divider {
  margin: 10% 0 2% 0;
  border-bottom: 1px solid rgba(0,0,0,0.12);
}

.middle-divider {
  margin: 3% 35% 3% 35%;
  border-bottom: 1px solid rgba(0,0,0,0.12);
}

p {
  font-size: 16px;
  max-width: 800px;
}

h4 {
  margin-bottom: 3vh;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  color: #741213;
}

.footer {
  color: #27487c;
}

.character-portrait-grid {
  max-width: 90%;
  margin: 0 auto;
}

.character-portrait-container {
  padding-bottom: 15px;
  padding-top: 15px;
  width: 20vh;
}

.character-portrait {
  object-fit: cover;
  object-position: 50% 0;
  width: 100px;
  height: 100px;
}

a {
  display: block;
  margin-top: 1vh;
  font-weight: 500;
  font-size: 18px;
}

.media-container {
  max-width: 768px;
  width: 90%;
}

.media {
  max-width: 768px;
  /*width: 90%;*/
  max-height: 240px;
  margin: 0 auto;
  padding: 2vh;
  border: 1px solid rgba(0,0,0,0.12);
  border-radius: 8px 8px 8px 8px;
  background-color: #afa08a;
  color: #f9f9f9;
}

.media-body {
  width: 60%;
}

.media-image {
  margin-left: 20px;
  border: 1px solid #ececec;
  max-width: 50%;
  max-height: 220px;
}

.return-link {
  margin-top: -15px;
  margin-bottom: 25px;
}

.session {
  margin-top: 3%;
}

.icon-container {
  color: #cecece;
}

.fa-times {
  cursor: pointer;
}

.session-img {
  max-width: 600px;
  width: 100%;
  margin-top: 15px;
  margin-bottom: 25px;
}